@charset "utf-8";
/****************************************
scroll START
****************************************/
/* width */
::-webkit-scrollbar {
    width: 4px;
}
  
/* Track */
::-webkit-scrollbar-track {
    background:#e8e8e8;
}
  
/* Handle */
::-webkit-scrollbar-thumb {
    background: #c8c8c8;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #c8c8c8;
}

_:-ms-lang(x), .scroll {//explore scroll 
    overflow: hidden;
    position: relative;
}  

_:-ms-lang(x), .scroll-list {//explore scroll 
    overflow-y: auto;
    margin-right: -10px;

    scrollbar-base-color: white;
    scrollbar-face-color:  #9b9b9b;
    scrollbar-3dlight-color:  white;
    scrollbar-highlight-color: white;
    scrollbar-track-color:    white;
    scrollbar-arrow-color: white;
    scrollbar-shadow-color: white;
}
/****************************************
scroll END
****************************************/