@charset "utf-8";
/****************************************
component: button START
****************************************/
.btn-fn {
    width: 86px;
    height: 33px;
    font-size: 14px;
    font-weight: bold;
    letter-spacing: -1px;
    border-radius: 20px;
}

.btn-outline-brand  {
    border: 1px solid $border-color;
    color: $brand;
}

.btn-outline-secondary {
    border: 1px solid $border-color;
    color: $text-dark;
}

.btn-brand {
    border: 1px solid $brand;
    background-color: $brand;
    color: white;

    &:disabled {
        border: 1px solid #ccc;
        background-color: #ccc;
    }
}

.btn-go {
    display: inline-block;
    height: 33px;
    line-height: 31px;
    padding-left: 19px;
    padding-right: 14px;
    font-size: 14px;
    font-weight: bold;
    letter-spacing: -1px;
    border-radius: 3px;
    color: $brand;
    background-color: #ffefef;

    i {
        display: inline-block;
        position: relative;
        top: -2px;
        width: 5px;
        height: 5px;
        margin-left: 10px;
        border: 1px solid $brand;
        border-left: none;
        border-bottom: none;

        @include rotate(45);
    }
}

.btn-lg {
    width: 100%;
    height: 56px;
    line-height: 54px;
    font-size: 18px;
    font-weight: bold;
    letter-spacing: -1px;
    color: white;
    border-radius: 5px;

    &.type-secondary {
        background-color: #cccccc;
    }
}
/****************************************
component: button END
****************************************/