@charset "utf-8";
/****************************************
global START
****************************************/
body, div, p, a, span, strong, h1, h2, h3, h4, h5, h6, input, textarea, select, button, table { 
    font-family:  AppleGothic, '맑은 고딕', 'Malgun Gothic', '돋움', Dotum, Helvetica, Sans-serif; 
    font-size: $font-size-base; 
    vertical-align: baseline; 
    color:$text-base; 
    word-break: keep-all; 
    word-wrap: break-word; 
}

.l-wrap {
    width: 1600px;
    margin: 0 auto;
}

.font-g {
    font-family: 'G Sans';
}

/****************************************
global END
****************************************/
