/****************************************
forms START
****************************************/
.form-control {
    width: 100%;
    height: 46px;
    padding: 0 15px;
    border: 1px solid #dfdfdf;
    font-size: 16px;
    color: $text-dark;
}

.form-box {
    padding: 0 60px;
    border: 1px solid #dddddd;
    border-radius: 5px;
}

.form-row {
    + .form-row {
        margin-top: 30px;
    }

    label {
        display: block;
        font-size: 16px;
        color: $text-mute;
        opacity: 1;
        transition: all .3s;
    
        -webkit-transform: translateY(-35px);
        -ms-transform: translateY(-35px);
         transform: translateY(-35px);
    
        -webkit-transform-origin: 0 0;
        -ms-transform-origin: 0 0;
         transform-origin: 0 0;
    }
    
    input {
        width: 100%;
        padding-top: 10px;
        padding-bottom: 14px;
        border-width: 2px;
        border-color: #e5e5e5;
        border-style: none none solid none;
        border-radius: 0;
        font-size: 16px;
        color: $text-dark;
        transition: all .3s;
    }
    
    input::placeholder {
        color: transparent;
    }
    
    input:-ms-input-placeholder {
        color: transparent;
    }
    
    input {
        &:focus {
            box-shadow: none;
            outline: none;
            border-color: $brand;
        }

        &:hover {
            border-color: $brand;
        }
    }
    
    input:valid ~ label,
    input:focus ~ label {
        -webkit-transform: translateY(-65px) scale(0.8);
        -ms-transform: translateY(-65px) scale(0.8);
         transform: translateY(-65px) scale(0.8);
    }
}

.form-row {
    position: relative;
    height: 47px;

    .btn-fn {
        position: absolute;
        top: 0;
        right: 0;
    }
}


/****************************************
forms END
****************************************/