/****************************************
component: modal START
****************************************/
.modal-backdrop {
    z-index: 10;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: #b2b2b2;
    opacity: 0.5;
}

.modal { 
    z-index: 15;
    overflow: hidden;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;  

    @include flexbox();
    @include align-items(center);
    @include justify-content(center);
}

.modal-dialog {
    position: relative;
    
    @include flexbox();
    @include align-items(center);
}

.modal-content {
    width: 100%;
    background-color: white;

    @include flexbox();
    @include flex-direction(column);
}

.modal-header {
    position: relative;
    height: 63px;
    padding-top: 40px;
    text-align: center;

    h2 {
        font-size: 24px;
        font-weight: bold;
        color: #111111;
    }

    .btn-modal-close {
        position: absolute;
        top: 20px;
        right: 20px;

        @include bg(18, 18, btn-modal-close);
    }
}

.modal-body {
    padding: 30px 40px 0 40px;
    text-align: center;
}

.modal-footer {
    margin-top: 20px;
    margin-bottom: 50px;
    text-align: center; 
}
/****************************************
component: modal END
****************************************/