@charset "utf-8";
/****************************************
메인 START
****************************************/
.page-main {
    .main-visual-wrap {
        overflow: hidden;
        height: 521px;
        // perspective: 500; //
        // transform-style: preserve-3d;
        // -ms-transform-style: preserve-3d;

        // transform:perspective(500px);
        // -ms-transform:perspective(500px); 
    }

    .main-visual {
        height: 521px;
        margin: 0 auto;
        padding-top: 150px;

        @include bg-pos(50%, 50%, main-visual-bg);

        // -ms-animation: cssAnimation 6s ease; //
        // -webkit-animation: cssAnimation 6s ease; //
        // animation: cssAnimation 6s ease; //
    }

    .main-visual-txt-1 {
        @include bg(396, 108, main-visual-txt-1);

        // /*effect Start*/
        // @include translateX(-800px);  
        // @include transition(all, 0.8s); 

        // &.on {
        //     opacity: 1;

        //     @include translateX(0); 
        // }
        // /*effect END*/
    }

    .main-visual-txt-2 {
        margin-left: 10px;

        @include bg(428, 58, main-visual-txt-2);

        // /*effect Start*/
        // @include translateX(-800px);  
        // @include transition(all, 0.8s); 

        // &.on {
        //     opacity: 1;

        //     @include translateX(0); 
        // }
        // /*effect END*/
    }

    .main-visual-txt-3 {
        margin-top: 10px;
        margin-left: 30px;
        
        @include bg(318, 16, main-visual-txt-3);

        // /*effect Start*/
        // @include translateY(800px); 
        // @include transition(all, 0.8s);   

        // &.on {
        //     opacity: 1;

        //     @include translateY(0); 
        // }
        // /*effect END*/
    }

   /*effect Start*/
    @keyframes cssAnimation {
        0% {
            transform: scale(1.2) translate(0px);
        }
        100% {
            transform: scale(1.0) translate(0px);
        }
    }

    @-webkit-keyframes cssAnimation {
        0% { 
            -webkit-transform: scale(1.2) translate(0px);
        }
        100% {
            -webkit-transform: scale(1.0) translate(0px);
        }
    }

    @-ms-keyframes cssAnimation {
        0% {
            -ms-transform: scale(1.2) translate(0px)
        }
        100% {
            -ms-transform: scale(1.0) translate(0px);
        }
    }
    /*effect END*/

    .w-inner {
        width: 1320px;
        margin: 0 auto;
    }

    section {
        margin-top: 50px !important;
    }

    .sc-tit {
        margin-bottom: 25px;
        font-size: 24px;
        font-weight: bold;
        color: $text-dark;
    }

    .btn-go {
        margin-bottom: 25px;  
    }

    .pro-list {
        @include flexbox();
        @include flex-wrap(wrap);
    }

    .pro-item {
        width: 424px;
        height: 320px;
        border: 1px solid #ddd;

        + .pro-item {
            margin-left: 24px;
        }

        .header-wrap {
            height: 189px;
            padding: 20px 20px 0 20px;
        }

        .header {
            @include flexbox();
        }

        h2 {
            font-size: 24px;
            color: #111;
        }

        small {
            margin-top: 20px;
            font-size: 13px;
            color: $mute;
        }

        p {
            margin-top: 25px;
            font-size: 15px;
            color: $text-dark;
        }

        .status {
            width: 60px;
            height: 60px;
            margin-left: auto;
            font-size: 13px;
            text-align: center;
            color: white;
            border-radius: 100%;

            @include flexbox();
            @include align-items(center);
            @include justify-content(center);

            &.type-ongoing {
                background-color: #41aa5e;
            }
    
            &.type-closing {
                background-color: $brand;
            }
    
            &.type-done {
                background-color: $mute;
            }
        }

        a {
            display: inline-block;
            margin-left: 10px;
            font-size: 14px;
        }

        .btn-sm {
            width: 66px;
            height: 24px;
            line-height: 24px;
            font-size: 12px;

            &.btn-round {
                border-radius: 20px;
            }

            &.type-ongoing {
                border: 1px solid #41aa5e;
                color: #41aa5e;
            }

            &.type-closing {
                border: 1px solid $brand;
                color: $brand;
            }

            &.type-done {
                border: 1px solid $mute;
                color: $mute;
            }

            &.type-standby {
                border: 1px solid #d99e00;
                color: #d99e00;
            }
        }
    }

    .sites {
        margin-top: 30px;

        @include flexbox();
        @include align-items(center);

        a {
            padding-left: 23px;
            font-size: 14px;
            color: $text-dark;

            + a {
                margin-left: 20px;
            }
        }
    }

    .link-jira {
        @include bg-pos(0, 3px, icon-jira);
    }

    .link-confluence {
        @include bg-pos(0, 3px, icon-confluence);
    }

    .link-bitbucket {
        @include bg-pos(0, 3px, icon-bitbucket);
    }

    .link-jenkins {
        @include bg-pos(0, 3px, icon-jenkins);
    }

    .scroll {
        overflow-x: hidden;
        overflow-y: auto;
        height: 82px;
    }

    .details {
        height: 129px;
        padding: 25px;
        background-color: #fafafa;

        .scroll {

        }
    }

    .details-list {
        overflow-y: auto;
        height: 82px;
    }

    .details-item {
        font-size: 14px;
        line-height: 24px;

        + .details-item {
            margin-top: 5px;
            
        }

        a {
            &:hover {
                color: #111;
            }
        }
    }
    
    .tabs {
        position: relative;
        bottom: -1px;

        @include flexbox();
        @include align-items(center);

        a {
            display: block;
            position: relative;
            height: 48px;
            margin-right: -1px;
            border: 1px solid #e8e8e8;
            line-height: 48px;
            text-align: center;
            background-color: #fafafa;
            flex: 1; margin-right: -1px;

            &:hover,
            &.on {
                margin-right: 0;
                border: 1px solid #bbbbbb;
                border-bottom: 1px solid transparent;
                color: $brand;
                background-color: white; 
            }
        }
    }

    .pannel {
        display: none;
    }

    .chart-container {
        padding: 40px 80px;
        border: 1px solid #bbbbbb;
    }

    .chartjs-render-monitor {
        height: 400px !important;
    }

    .sc-aside {
        margin-top: 50px;

        @include flexbox();
    }

    .notice {
        width: 635px;
        margin-right: 40px;
    }

    .notice-list {
        li {
            line-height: 30px;

            @include flexbox();
            @include align-items(center);
        }

        small {
            margin-left: auto;
            font-size: 14px;
            color: $text-mute;
        }

        a {
            display: block;
            width: 480px;
            font-size: 15px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;

            &:hover {
                color: #111;
            }
        }
    }

    .download-list {
        width: 680px;
        margin-top: 10px;
        margin-left: auto;

        @include flexbox();

        li {
            flex: 1;
            width: 324px;
            height: 130px;

            @include flexbox();

            &:first-child {
                margin-right: 15px;

                a {
                    background-color: #f6faff;

                    @include bg-pos(250px, 40px, main-ban-img-1);

                    &:hover {
                        background-color: #ebf4fc;
                    }
                }
            }

            &:last-child {
                a {
                    background-color: #fdf5f5;

                    @include bg-pos(230px, 40px, main-ban-img-2);

                    &:hover {
                        background-color: #faecec;
                    }
                }

            }
        }

        a { 
            display: block;
            width: 100%;
            height: 100%;
            padding-left: 40px;
            padding-right: 34px;

            @include flexbox();
            @include justify-content(center);
            @include flex-direction(column);
        }
        p {
            display: block;
            color: $text-dark;
        }

        span {
            display: block;
            margin-top: 15px;
            color: $text-mute;
        }
    }

    .btn-more {
        margin-left: auto;

        @include bg(13, 13, main-btn-more);
    }
}

/****************************************
메인 END
****************************************/