.modal-dialog.search-company {
    .search-box {
        dt {
            width: 65px;
            text-align: left;
            color: $text-dark;
        }

        dd {
            width: 270px;
        }

        input {
            width: 100%;
        }
    }

    .tbl-wrap {
        margin-top: 30px;
        margin-bottom: 50px;
        border-top: 2px solid #000000;
        border-bottom: 1px solid #bbbbbb;
    }

    .scroll {
        height: 255px;
        overflow-y: auto;
    }

    li {
        border-bottom: 1px solid #e8e8e8;
        line-height: 50px;
        text-align: left;

        &:last-child {
            border-color: transparent;
        }
    }

    a {
       &:hover {
        color: $brand;
       }
    }
}

.no-data {
    height: 155px;
    
    @include flexbox();
    @include flex-direction(column);
    @include justify-content(center);
    @include align-items(center);

    p {
        font-size: 14px;

        @include flexbox();
        @include align-items(center);
    }

    i {
        margin-right: 5px;

        @include bg(13,13,nodata-icon-info);
    }

    button {
        margin-top: 25px;
        height: 33px;
        width: 125px;
        font-size: 14px;
        border-radius: 5px;
    }
}