@charset "utf-8";

/****************************************
   Setting START
****************************************/
* {
	margin: 0;
	padding: 0;

	box-sizing: border-box; 

	&:before,
	&:after {
		box-sizing: border-box;
	}
}

html, body {
	// -webkit-font-smoothing: antialiased;
	// -moz-osx-font-smoothing: grayscale;
	// -ms-text-size-adjust: 100%;
	// -webkit-text-size-adjust: 100%;
}

ul, ol, li {
	list-style: none;
}

img, button, fieldset {
	border: 0 none;
}

table {
	width: 100%;
	border-collapse: collapse;
	border-spacing: 0;
	table-layout: fixed;
}

em, address {
	font-style: normal;
}

i {
	display: inline-block;
	font-style: normal;
}

a {
	display: inline-block;
	text-decoration: none;
	color: inherit;
	cursor: pointer;
}

fieldset, legend {
	border: 0;
}

legend {
	height: 0;
	visibility: hidden;
}

button {
	background: none;
}

button * {
	position: relative;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: none;
}

input::-ms-clear,
input[type=password]::-ms-reveal {
	display: none;
}

input, textarea, select {
	box-sizing: border-box; 
}

textarea {
	width: 100%;
	resize: none;
}

a, button, input, select, textarea, [contenteditable] {
	outline: 0;
}

button, select, input[type=checkbox], input[type=radio] {
	cursor: pointer;
}

button:disabled, input:disabled {
	cursor:default;
}

[aria-hidden="true"] {
	display:none;
}

article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section,main {
    display: block;
    border: 0;
    *margin: 0;
    *padding: 0;
    *font-size: 0
}

h1, h2, h3, h4, h5, h6 {
	font-size: $font-size-base;
	font-weight: normal;
}
/****************************************
Setting END
****************************************/