@charset "utf-8";
/****************************************
flex START
****************************************/
.d-flex {
    @include flexbox();
}

.align-justify-center {
    @include flexbox();
    @include align-items(center);
    @include justify-content(center);
}

.align-center {
    @include flexbox();
    @include align-items(center);
}

.align-start {
    @include flexbox();

    -webkit-box-align: start !important; 
    -ms-flex-align: start !important; 
    align-items: flex-start !important;
}

.justify-center {
    @include flexbox();
    @include justify-content(center);
}

.ml-auto {
    margin-left: auto;
}
/****************************************
flex END
****************************************/
