@charset "utf-8";
/****************************************
l-header START
****************************************/
.logo {
    a {
        display: block;
        width: 110px;
        height: 90px;

        @include bg-pos(0, 30px, logo);
    }
} 

.l-header {
    height: 90px;
    margin:0 120px auto; 

    @include flexbox();
    @include align-items(center);

    dl {
        margin-left: 80px;

        @include flexbox();
        @include align-items(center);
    }

    dt {
        color: $text-dark;
    }

    dd {
        padding-left: 10px;
        font-size: 14px;

        &:first-child {
            color: $mute;
        }
    }

    .links {
        margin-left: auto;

        @include flexbox();
        @include align-items(center);

        a  {
            padding-right: 21px;
            font-size: 18px;
            color: $text-dark;
    
            @include bg-pos(100%, 5px, header-arrow);
    
            + a {
                margin-left: 30px;  
            }
        }

        i {
            vertical-align: middle;
    
            @include bg(16, 16, header-arrow);
        }
    }
}
/****************************************
l-header END
****************************************/