@charset "utf-8";
/****************************************
로그인 START
****************************************/
.page-login {
    .login-box {
        width: 960px;
        height: 360px;
        margin: 0 auto;
        margin-top: 35px !important;
        padding: 0;

        @include flexbox();
        @include align-items(center);
    }

    .btn-lg {
        margin-top: 20px;
    }

    .login-item {
        width: 480px;
        height: 100%;
        padding: 60px 60px 0 60px;
    }

    h3 {
        padding-left: 10px;
        font-size: 17px;

        @include bg-pos(0, 12px, join-ico-dot);

        + .btn-go {
            margin-left: 10px;
        }
    }

    .btn-go {
        width: 125px;
        padding-left: 0;
        padding-right: 0;
        text-align: center;
    }

    .notice {
        border-left: 1px solid #eeeeee;
    }

    .notice-item {
        margin-top: 15px;
        padding-left: 18px;
        font-size: 14px;
        line-height: 24px;
        letter-spacing: -0.5px;
        color: #767676;

        @include bg-pos(0, 7px, login-icon-notice);
    }

    .links {
        margin-top: 20px;

        @include flexbox();

        a {
            display: inline-block;
            padding-left: 15px;
            padding-right: 15px;
            font-size: 14px;
            letter-spacing: -1px;
            white-space: nowrap;

            &:hover {
                color: #e22e37;
            }

            &:first-child { 
                padding-left: 0;
                background-image: none;
            }

            @include bg-pos(0, 5px, login-bg-link);
        }
    }
}
/****************************************
로그인 END
****************************************/