@charset "utf-8";
/****************************************
common-ui START
****************************************/
.ui-header {
    margin-top: 50px !important;
    font-family: 'G Sans';
    text-align: center;

    h2 {
        font-size: 42px;
        font-weight: bold;
        letter-spacing: -1px;
        color: $text-dark;
    }

    p {
        margin-top: 35px;
        font-size: 16px;
        line-height: 28px;
    }

    strong {
        display: inline-block;
        margin-top: 35px;
        padding-left: 13px;
        font-size: 17px;
        font-weight: normal;

        @include bg-pos(0, 12px, join-icon-dot)
    } 
}
/****************************************
common-ui START
****************************************/