@charset "utf-8";
/****************************************
aside START
****************************************/
.l-aside {
    z-index: 60;
    position: absolute;
    right: 0;
    top: -20px;
    width: 420px;
    padding: 18px 20px 0 18px;
    border: 1px solid #e8e8e8;
    box-shadow: 0 3px 3px #e8e8e8;
    background-color: white;

    @include transition(all, 800ms);
    @include translateX(1000px);

    &.on {
        @include translateX(20px);
        
        .btn-l-aside-close {
            span {
                background-color: transparent;
                transition-delay: 800ms;

                &:before,
                &:after {
                    transition-delay: 800ms;

                    @include transition(all, 800ms);
                }

                &:before { 
                    top: 0;

                    @include rotate(45);
                }

                &:after {
                    bottom: 0;
                    
                    @include rotate(-45);
                }
            }
        }
    }

    header {
        @include flexbox();
        @include align-items(center);

        .user-status {   
            margin-right: 8px;
             
            @include bg(46, 46, icon-user-aside);
        }
    
        h1 {
            padding-right: 30px;
            line-height: 44px;
            color: $text-dark;
            cursor: pointer;
    
            @include bg-pos(100%, 12px, icon-logout); 
        }    
    }

    .btn-l-aside-close {
        width: 42px;
        height: 42px;
        margin-left: auto;
        border-radius: 100%;
        background-color: #ff442b;
    
        span {
            z-index: 5;
            position: relative;
            top: -4px;
            left: 0;
            display: inline-block;
            width: 16px;
            height: 2px;
            background-color: white;

            &:after,
            &:before {
                content:"";
                display: block;
                position: absolute;
                left: 0;
                width: 16px;
                height: 2px;
                background-color: white;
            }

            &:before {
                top: -6px;
            }

            &:after {
                bottom: -6px;
            }
        }
    }

    .content {
        margin-top: 25px;
        margin-bottom: 40px;
        
        .txt-info {
            padding-left: 19px;
            font-size: 14px;
            line-height: 22px;
            color: #888888 !important;

            @include bg-pos(0, 2px, icon-speaker);
        }

        ul {
            margin-top: 30px;

            @include flexbox();
            @include flex-wrap(wrap);
        }

        li {
            width: 180px;
            text-align: center;

            &:nth-child(2n) {
                margin-left: 7px;
            }

            &:first-child {
                h2 {
                    @include bg-pos(50%, 100%, icon-mydesk-detail-1);
                }
            }

            &:nth-child(2) {
                h2 {
                    @include bg-pos(50%, 100%, icon-mydesk-detail-2);
                }
            }

            &:nth-child(3) {
                margin-top: 35px;

                h2 {
                    @include bg-pos(50%, 100%, icon-mydesk-detail-3);
                }
            }

            &:nth-child(4) {
                margin-top: 35px;

                h2 {
                    @include bg-pos(50%, 100%, icon-mydesk-detail-4);
                }
            }

            &:nth-child(5) {
                margin-top: 35px;

                h2 {
                    @include bg-pos(50%, 100%, icon-mydesk-detail-5);
                }
            }

            &:last-child {
                margin-top: 35px;

                h2 {
                    @include bg-pos(50%, 100%, icon-mydesk-detail-6);
                }
            } 
        }

        h2 {
            height: 90px;  
            font-size: 16px;
        }

        .details {
           height: 33px;
           margin-top: 15px;
           font-size: 14px;
           line-height: 33px;
           letter-spacing: -1px;
           border-radius: 20px;
           color: #888888;
           background-color: #f6f6f6;

            select {
                padding-right: 20px;
                font-size: inherit;

                @include bg-pos(100%, 50%, aside-arrow-select)
            }
        }
    }
}
/****************************************
aside END
****************************************/