@charset "utf-8";
/****************************************
가입하기 START
****************************************/
.page-join {
    .w-inner {
        width: 480px;
        margin: 0 auto;
    }

    .form-box {
        margin-top: 35px !important;
    }

    .btn-lg {
        margin-bottom: 50px;
    }
}

.page-join.register {
    header {                  
        button {
            margin-top: 15px; 
            font-size: 14px;
        }

        .btn-go {
            margin-top: 20px;
        }
    }

    li {
        &:first-child {
            margin-top: 55px;
        }
    }

    .form-box {
        padding-top: 55px;
    }

    .btn-lg {
        margin-top: 20px;
    }
}
/****************************************
가입하기 END
****************************************/
/****************************************
가입완료 START
****************************************/
.page-join.done {
    header {
        p {
            font-size: 16px;
            line-height: 28px;
        }
    }

    ul {
        width: 350px;
        margin-top: 50px;

        li {
            text-align: center;

            &:first-child {
                padding-top: 35px;
                padding-bottom: 35px;
                font-size: 24px;
                color: $text-dark;
                border-radius: 5px;
                background-color: #f7f7f7;
            }

            &:last-child {
                margin-top: 20px;
                margin-bottom: 30px; 
            }
        }
    }
}
/****************************************
가입완료 END
****************************************/
/****************************************
FIDO 가입 START
****************************************/
.page-join.fido {
    ul {
        margin-top: 50px;
        margin-bottom: 30px;
        text-align: center;
    }

    li + li {
        margin-top: 20px;
    }

    input {
        width: 360px;
        height: 90px;
        padding: 0 82px;
        font-size: 24px;
        line-height: 90px;
        border-radius: 5px;
        background-color: $bg-base;
        
        @include bg-pos(74px, 36px, bg-placeholder);

        &:focus {
            background-image: none;
        }
    }
}
/****************************************
FIDO 가입 END
****************************************/